import { TimePicker } from 'calendar';
import React from 'react';
import { block, Playground } from 'dev/playground';

export function Example() {
  const [value, onChange] = React.useState('')
  return (
    <Playground>
      <div style={{ paddingBottom: '200px', display: 'flex' }}>
        <TimePicker
          format="HH:mm:ss"
          priority="default"
          name="time"
          value={value}
          onChange={onChange}
          hourStep={2}
          minStep={5}
          secStep={10}
        />
      </div>
    </Playground>
  )
}